@import url('https://fonts.googleapis.com/css2?family=Metal+Mania&display=swap');
.quiz-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 2px solid #4caf50;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    font-family: "Arial,Helvetica", serif;
  font-weight: 400;
  font-style: normal;
  }
  
  .quiz-progress {
    margin-top: 15px;
    font-size: 28px;
    color: #333;
  }
  
  .quiz-container h2 {
    font-size: 28px;
    color: #333;
  }
  
  .quiz-container ul {
    list-style-type: none;
    padding: 0;
  }
  
  .quiz-container li {
    margin: 10px 0;
    padding: 10px;
    background: #e8f5e9;
    border: 1px solid #035205;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.3s ease;
    font-size: 24px;
  }
  
  .quiz-container li:hover {
    background: #4caf50;
    color: white;
    transform: scale(1.03);
  }