@import url('https://fonts.googleapis.com/css2?family=Diplomata&display=swap');
*{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  margin: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  color: #4CAF50;
  font-family: "Diplomata", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 35px;
  margin-bottom: 35px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 10px 0;
  padding: 10px;
  background: #f0f0f0;
  border: 1px solid #ccc;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s ease;
}

li:hover {
  background: #ddd;
}